.picture {
  border-radius: 50%;
  border: 1px solid white;
  overflow: hidden;
  width: 150px;
  height: 150px;
  margin: 1.3em;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logos {
  padding: 5px 5px 5px 5px;
}

.linkedin {
  width: 25px;
  height: 20px;
}

.github {
  width: 20px;
  height: 20px;
}

.title {
  font-size: 1.2em;
  margin: 0;
}
